<template>
<transition name="fade">
  <div class="container">
    <div id="myChart" class="map">
    </div>
  </div>
</transition>
</template>

<script>
// 引入基本模板
var echarts = require('echarts')
import "echarts/map/js/china.js";
import "echarts/lib/component/title";
import "echarts/lib/component/legend";
import "echarts/lib/chart/heatmap";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
import {GRID} from "@/jslib/areaAddress";
import {mapState} from 'vuex'
const STOREDATA = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
const YEARLIST = [2013, 2014, 2015, 2016, 2017, 2018]
export default {
  data() {
    return {
      maxVal: 500
    };
  },
  created() {
    // console.log(this.findGrid("河北", "邯郸"))
    // console.log(this.getProvinceData(2016, 4))
    // console.log(this.returnMaxVal(2016, 4))
  },
  mounted() {
    this.$nextTick(() => {
      this.drawMap();
    })
  },
  methods: {
    // 查找城市、省份坐标
    findGrid(province, city) {
      if (!city) {
        let grid = []
        for (let index in GRID) {
          if (GRID[index].name == province) {
            grid.push(GRID[index].log);
            grid.push(GRID[index].lat);
            break;
          }
        }
        return grid;
      } else {
        let grid = []
        outside:
        for (let index in GRID) {
          if (GRID[index].name == province) {
            let citylist = GRID[index].children
            for (let cityIndex in citylist) {
              if(citylist[cityIndex].name == city) {
                grid.push(citylist[cityIndex].log);
                grid.push(citylist[cityIndex].lat);
                break outside;
                break;
              }
            }
          }
        }
        return grid;
      }
    },
    // 计算省份的数据
    getProvinceData(year, type) {
      // 选项的type值如下
      // "PM 2.5"：type: 0
      // "NO₂"：type: 1
      // "PM 10"：type: 2
      // "O₃"：type: 3
      // "SO₂"：type: 4
      // "CO"：type: 5
      let data = [];
      GRID.forEach(province => {
        let proVal = {
          name: province.name,
          year: year
        };
        let val = 0;
        this[STOREDATA[type]].forEach(city => {
          if (city['省份'] == province.name) {
            if(!isNaN(city[year])){
              val += Number(city[year])
            }
          }
        });
        if (val > 0) {
          let ValueArray = [Number(province.log), Number(province.lat), val];
          proVal.value = ValueArray;
          data.push(proVal);
        }
      })
      return data
    },
    // 计算城市数据
    getCityData(year, type) {
      let data = [];
      this[STOREDATA[type]].forEach(city => {
        let cityVal = {
          name: city["城市"],
          year: year
        };
        let val = (isNaN(city[year]) || city[year] === "") ? 0 : city[year]
        let ValueArray = [Number(city.LNGB), Number(city.LATB), val];
        cityVal.value = ValueArray
        data.push(cityVal)
      })
      return data
    },
    // 计算重点城市
    getImportantCityData(year, type) {
      let data = [];
      let pollutant = this[STOREDATA[type]];
      this.importantCity.forEach(Icity => {
        for (let index in pollutant) {
          if (pollutant[index]['城市'] == Icity['城市']) {
            let cityVal = {
              name: Icity['城市'],
              year: year
            };
            let val = (isNaN(pollutant[index][year]) || pollutant[index][year] === "") ? 0 : pollutant[index][year]
            let ValueArray = [Number(pollutant[index].LNGB), Number(pollutant[index].LATB), val];
            cityVal.value = ValueArray
            data.push(cityVal);
            break;
          }
        }
      })
      return data;
    },
    // 计算几大区域城市数据
    getAreaData(year, type, areaIndex) {
      let data = [];
      let pollutant = this[STOREDATA[type]];
      let areaList = ['占位', '占位', '占位', '京津冀', '长三角', '珠三角'];
      let area = areaList[areaIndex];
      let queryCityList = [];
      
      this.hotCity.forEach(city => {
        if (city['区域'] == area) {
          queryCityList.push(city['城市']);
        }
      })
      console.log(queryCityList)
      queryCityList.forEach(city => {
        for (let index in pollutant) {
          if (city == pollutant[index]['城市']) {
            let cityVal = {
              name: city,
              year: year
            };
            let val = (isNaN(pollutant[index][year]) || pollutant[index][year] === "") ? 0 : pollutant[index][year]
            let ValueArray = [Number(pollutant[index].LNGB), Number(pollutant[index].LATB), val];
            cityVal.value = ValueArray
            data.push(cityVal);
            break;
          }
        }
      })
      return data;
    },
    // returnMaxVal(year, type) {
    //   let STOREDATA = ['PM2d5', 'NO2', 'PM10', 'O3', 'SO2', 'CO']
    //   let data = this[STOREDATA[type]];
    //   let lg = data.length-1;
    //   return data[lg][year];
    // },
    toolsMaxVal(data) {
      let arr = [];
      data.forEach(obj => {
        arr.push(obj.value[2])
      })
      return Math.max(...arr)
      // console.log(arr)
    },
    drawMap() {
      let _this = this;
      this.myChart = echarts.init(document.getElementById("myChart"));
      let defaultData = this.getProvinceData(YEARLIST[_this.chartData.heatYear], _this.chartData.pollutant)
      let max = this.toolsMaxVal(defaultData) > 0 ? this.toolsMaxVal(defaultData) : 200;
      let mapType = this.chartData.area === 1 ? "scatter" : "heatmap"
      this.myChart.setOption({
        tooltip: {
          trigger: "item",
          // backgroundColor:"#fff",
          position: "top",
          formatter: function(params) {
            let text =
              "年份:" +
              params.data.year +
              "<br>" +
              "省份:" +
              params.name +
              "<br>" +
              "污染值:" +
              params.value[2];
              // console.log(params);
            return text;
          }
        },
        toolbox: {
          show: true,
          showTitle: true,
          orient: "vertical",
          right: "20px",
          top: "center",
          feature: {
            mark: { show: true },
            dataView: { show: true, readOnly: false },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        visualMap: {
          min: 0,
          max: max,
          calculable: true, //左下角的颜色条开启
          inRange: {
            color: ["#FE2501", "#EBED11", "#9AFA61", "#A5BCF5"].reverse()
          },
          textStyle: {
            color: "#333"
          }
        },
        geo: {
          map: "china",
          label: {
            emphasis: {
              show: false
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: "#9398a3",
              borderColor: "#fff"
            },

            emphasis: {
              //鼠标hover 地图省份的背景
              areaColor: "#2a333d"
            }
          }
        },
        series: [
          {
            name: "中国",
            // type: "heatmap",
            // type: "scatter",
            type: mapType,
            coordinateSystem: "geo",
            // data: [
            //   {
            //     year: "2013",
            //     name: "广州",
            //     value: [113.270813674, 23.1351666766, 9620]
            //   },
            //   {
            //     year: "2013",
            //     name: "深圳",
            //     value: [114.0661345267, 22.5485544122, 145874]
            //   },
            // ],
            data: defaultData,
            itemStyle: {
              normal: {
                borderColor: "#271b39",
                borderWidth: 1,
                areaColor: "#675c74"
              },
              emphasis: {
                areaColor: "#d8d5e6"
              }
            }
          }
        ]
      });
      setTimeout(function(){
        window.onresize = function(){
        _this.myChart.resize(); 
        }
      },200)
    },
  },
  computed: {
    ...mapState({
        PM2d5: 'PM2d5',
        CO: 'CO',
        NO2: 'NO2',
        PM10: 'PM10',
        SO2: 'SO2',
        O3: 'O3',
        chartData: 'chartData',
        importantCity: 'importantCity',
        hotCity: 'hotCity',
    }),
  },
  watch: {
      chartData: {
        handler(newValue, oldValue) {
          let year = YEARLIST[newValue.heatYear]
          let data,type,max;
          let option = {
            tooltip: {
              trigger: "item",
              position: "top",
              formatter: function(params) {
                let text =
                  "年份:" +
                  params.data.year +
                  "<br>" +
                  "省份:" +
                  params.name +
                  "<br>" +
                  "污染值:" +
                  params.value[2];
                return text;
              }
            },
            toolbox: {
              show: true,
              showTitle: true,
              orient: "vertical",
              right: "20px",
              top: "center",
              feature: {
                mark: { show: true },
                dataView: { show: true, readOnly: false },
                restore: { show: true },
                saveAsImage: { show: true }
              }
            },
            visualMap: {
              min: 0,
              max: 200,
              calculable: true, //左下角的颜色条开启
              inRange: {
                color: ["#FE2501", "#EBED11", "#9AFA61", "#A5BCF5"].reverse()
              },
              textStyle: {
                color: "#333"
              }
            },
            geo: {
              map: "china",
              label: {
                emphasis: {
                  show: false
                }
              },
              roam: true,
              itemStyle: {
                normal: {
                  areaColor: "#9398a3",
                  borderColor: "#fff"
                },

                emphasis: {
                  //鼠标hover 地图省份的背景
                  areaColor: "#2a333d"
                }
              }
            },
            series: [
              {
                name: "中国",
                type: type,
                coordinateSystem: "geo",
                data: [0, 0, 0],
                itemStyle: {
                  normal: {
                    borderColor: "#271b39",
                    borderWidth: 1,
                    areaColor: "#675c74"
                  },
                  emphasis: {
                    areaColor: "#d8d5e6"
                  }
                }
              }
            ]
          }
          switch (newValue.area) {
            case 0:
              data = this.getProvinceData(year, newValue.pollutant)
              max = this.toolsMaxVal(data)
              option.series[0].type = "heatmap"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
              break;
            case 1:
              data = this.getCityData(year, newValue.pollutant)
              max = this.toolsMaxVal(data)
              option.series[0].type = "scatter"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
              break;
            case 2:
              data = this.getImportantCityData(year, newValue.pollutant)
              max = this.toolsMaxVal(data)
              option.series[0].type = "heatmap"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
              break;
            case 3:
              data = this.getAreaData(year, newValue.pollutant, newValue.area)
              max = this.toolsMaxVal(data)
              option.series[0].type = "heatmap"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
            case 4:
              data = this.getAreaData(year, newValue.pollutant, newValue.area)
              max = this.toolsMaxVal(data)
              option.series[0].type = "heatmap"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
            case 5:
              data = this.getAreaData(year, newValue.pollutant, newValue.area)
              max = this.toolsMaxVal(data)
              option.series[0].type = "heatmap"
              option.visualMap.max = max > 0 ? max : 200;
              option.series[0].data = data
              this.myChart.setOption(option)
              break;
            default:
              break;
          }
          // max = this.returnMaxVal(year, newValue.pollutant) * 5
          // this.myChart.setOption({
          //   series: [
          //     {
          //       // type: type,
          //       data: data,
          //     }
          //   ],
          //   visualMap:{
          //     max: max
          //   }
          // })
        },
        deep: true
      }
  },
};
</script>

<style scoped lang="scss">
.container {
  position: absolute;
  top: 60px;
  bottom: 0;
  left: 275px;
  right: 0;
}
.map {
  width: auto;
  height: 100%;
}
</style>
